import { createSlice } from "@reduxjs/toolkit";
import type { PayloadAction } from "@reduxjs/toolkit";
import { IColumnItem } from "./../Components/Column/Column";
import { ItemList } from "../Data/Data";
import moment from "moment";
import { useDispatch } from "react-redux";

export interface IEventItem {
  id: any;
  start?: Date;
  title: string;
  order: number;
  done : boolean;
}

export interface Todo {
  id: string;
  title: string;
  done: boolean;
  order: number;
}

export interface TodoState {
  todos: Todo[];
  columns: IColumnItem[];
  events: IEventItem[];
}

const sortCards = (a: any, b: any) => {
  if (a.order > b.order) {
    return 1;
  } else {
    return -1;
  }
};

export type ItemT = IEventItem | Todo;

const initialState: TodoState = {
  todos: ItemList,
  columns: [
    {
      order: 0,
      name: "1 ",
      tasks: ["", "  "],
    },

    {
      order: 1,
      name: "2 ",
      tasks: ["", "  "],
    },
  ],
  events: [
    {
      id: "1",
      order: 0,
      start: moment().startOf("day").toDate(),
      title: "  ",
      done: true,
    },
    {
      id: "2",
      order: 1,
      start: moment().startOf("day").toDate(),
      title: " ",
      done: false,
    },
    {
      id: "3",
      order: 2,
      start: moment().startOf("day").toDate(),
      title: "  ",
      done: false,
    },
    {
      id: "4",
      order: 3,
      start: moment().startOf("day").toDate(),
      title: "",
      done: false,
    },
  ],
};

export const todosSlice = createSlice({
  name: "todos",
  initialState,
  reducers: {
    setTodos: (state, action: PayloadAction<Todo[]>) => {
      state.todos = action.payload
    },
    editTodo: (state, action: PayloadAction<Todo>) => {
      state.todos = state.todos.map(todo => todo.id === action.payload.id ? action.payload : todo);
    },
    deleteTodo: (state, action: PayloadAction<Todo>) => {
      let todos = state.todos.filter((todo) => todo.id !== action.payload.id);
      for (let i = 0; i < todos.length; i++) {
        todos[i].order = i;
      }
      state.todos = todos;
    },
    deleteTodos: (state, action: PayloadAction<Todo[]>) => {
      const todos = state.todos.filter(
        (todo) => !action.payload.find((item) => item.id === todo.id)
      );
      state.todos = todos;
    },
    addTodo: (state, action: PayloadAction<Todo>) => {
      state.todos.unshift(action.payload);
      state.todos = state.todos.map((todo, index) => {
        return { ...todo, order: index };
      });
    },
    changeDone: (state, action: PayloadAction<Todo>) => {
      console.log("!action.payload.done", action.payload.done)
      const changedItemId = action.payload.order;
      
      if (!action.payload.done) {
        let todos = state.todos.map((todo) =>
        todo.id === action.payload.id ? { ...todo, done: !todo.done, order: state.todos.length-1 } : todo
        );
        for (let i = changedItemId; i < state.todos.length-1; i++) {
          todos[i+1].order = i;
        }
        state.todos = todos.sort(sortCards);
      } else {
        let todos = state.todos.map((todo) =>
          todo.id === action.payload.id ? { ...todo, done: !todo.done, order: 0 } : todo
        );
        for (let i = 0; i < state.todos.length; i++) {
          if (todos[i].id !== action.payload.id) {
            todos[i].order = i+1;
          }
        }
        
        state.todos = todos.sort(sortCards);
      }
      
      
    },


    setEvents: (state, action: PayloadAction<IEventItem[]>) => {
      console.log("qwwert", action.payload)
      state.events = state.events.map(event => {
        if (action.payload.find(el => event.id === el.id)) {
          return action.payload.filter(el => el.id === event.id)[0]
        } else {
          return event
        }
      }).sort(sortCards)
    },

    changeDoneEvent: (state, action: PayloadAction<IEventItem>) => {
      console.log("action.payload!", action.payload)
      
      state.events = state.events.map(event => {
        return event.id === action.payload.id ? {...event, done: !event.done} : event})
    },

    addEvent: (state, action: PayloadAction<IEventItem>) => {
      console.log("action.payload888", action.payload);
      state.events.push(action.payload);
    },
    editEvent: (state, action: PayloadAction<IEventItem>) => {
      state.events = state.events.map(event => event.id === action.payload.id ? action.payload : event);
    },
    deleteEvent: (state, action: PayloadAction<string>) => {
      console.log("action.payload", action.payload)
      state.events = state.events.filter((item) =>  item.id !== action.payload)
      const deletedEvent = state.events.find(el => el.id === action.payload)
      let oldDateEvents = state.events.filter(event => event.start?.toDateString() === deletedEvent?.start?.toDateString())
      oldDateEvents = oldDateEvents?.map((el, i) => {return {...el, order: i}})
      console.log("oldDateEvents ", oldDateEvents )
      
      state.events = state.events.map(event => {
        if (oldDateEvents.find(el => event.id === el.id)) {
          return oldDateEvents.filter(el => el.id === event.id)[0]
        } else {
          return event
        }
      }).sort(sortCards)
    }
  },
});

export const {
  deleteTodo,
  addTodo,
  deleteTodos,
  addEvent,
  deleteEvent,
  changeDone,
  setTodos,
  editTodo,
  editEvent,
  changeDoneEvent,
  setEvents
} = todosSlice.actions;

export default todosSlice.reducer;


