import FullCalendar from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../reducers';
import interactionPlugin, { Draggable } from '@fullcalendar/interaction';
import { addEvent, deleteEvent, editEvent, IEventItem, setEvents } from '../../reducers/newSlice';
import { useState } from 'react';
import "./Calendar.css";
import { AddTaskModal } from '../../Components/AddTaskModal/AddTaskModal';
import { generateGuid } from '../../Utils/GenerateGuid';
import { Menu } from '../../Components/Menu/Menu';
import { useEffect } from 'react';
import moment from 'moment';

export interface ICalendarProps {
  initialView?: string;
}



export const Calendar = (props: ICalendarProps) => {
  const events = useSelector((state: RootState) => state.todos.events);
  const [addModalOpen, setAddModalOpen] = useState<boolean>(false);
  const [editModalOpen, setEditModalOpen] = useState<boolean>(false);
  const [inputValue, setInputValue] = useState<string>("");
  const [currentDate, setCurrentDate] = useState<any>(null);
  const [currentDateEl, setCurrentDateEl] = useState();
  const dispatch = useDispatch()

  // useEffect(() => {
  //   document.querySelectorAll("tr[role='row']").forEach((el) =>{ 
  //     console.log("el", el);
  //     (el as HTMLElement).style.height = "0";
  //   })
  // })
  
  const eventChanged = (eventDropInfo:any) => {
    console.log("hi")
    console.log("eventDropInfo", eventDropInfo)
    if(eventDropInfo.oldEvent.start !== eventDropInfo.event.start) {
      dispatch(deleteEvent(eventDropInfo.oldEvent.id))
      // let oldDateEvents = events.filter(event => event.start?.toDateString() === eventDropInfo.oldEvent.start.toDateString())
      // oldDateEvents = oldDateEvents?.map((el, i) => {return {...el, order: i}})
      // console.log("oldDateEvents", oldDateEvents)
      // dispatch(setEvents(oldDateEvents))
      const currentDateEvents = events.filter(event => event.start?.toDateString() === eventDropInfo.event.start.toDateString())
      const currentEvent = {title: eventDropInfo.event.title, start:eventDropInfo.event.start, id: eventDropInfo.event.id, done: eventDropInfo.event.extendedProps.done, order: currentDateEvents.length}
      dispatch(addEvent(currentEvent))
    } else {
      console.log("qwertyutjj@")
    }
 
  }

  const eventDragStop = (info: any) => {
    console.log("info",info)
  }

  const addEventHandler = (dateClickInfo: any) => {
    (dateClickInfo.dayEl as HTMLElement).classList.add("selected");
    setCurrentDateEl(dateClickInfo.dayEl)
    setCurrentDate(dateClickInfo.date)
    setAddModalOpen(true);
  }

  const editEventHandler = (eventClickInfo: any) => {
    console.log(eventClickInfo)
    setCurrentDateEl(eventClickInfo.event)
    setInputValue(eventClickInfo.event.title)
    setEditModalOpen(true)
  }

  const editItem = () => {
    if (inputValue && currentDateEl && currentDateEl!==null) {
        const editTask = {
            id: (currentDateEl as any).id,
            title: inputValue,
            order: (currentDateEl as any).extendedProps.order,
            start: (currentDateEl as any).start,
            done: (currentDateEl as any).extendedProps.done

        }
        
        dispatch(editEvent(editTask))
        setEditModalOpen(false);
        setInputValue("");
    }
}

const deleteItem = (eventInfo: any) => {
  console.log("eventInfo", eventInfo.event.id)
  dispatch(deleteEvent(eventInfo.event.id))
}



function renderEventContent(eventInfo: any) {
  const event = {
    id: eventInfo.event.id,
    title: eventInfo.event.title,
    order: eventInfo.event.extendedProps.order,
    start: eventInfo.event.start,
    done: eventInfo.event.extendedProps.done
  }
  return (
    <>
      <div className="item-list-element">
        <div className={"event-element"}>{eventInfo.event.title}</div>
      </div>
      <Menu editItem={() => editEventHandler(eventInfo)} deleteItem={() => deleteItem(eventInfo)} dublicateItem={dublicateItem} task={event}/>
    </>     
  )
}

  const addItem = () => {
    currentDateEl && (currentDateEl as HTMLElement).classList.remove("selected")
    
    if (currentDate && currentDate !== null) {
      const currentDateEvents = events.filter(event => event.start?.toDateString() === currentDate.toDateString())
      const currentEvent = {title: inputValue, start: currentDate, id: generateGuid(), done: false, order: currentDateEvents.length}
      dispatch(addEvent(currentEvent))
    }
    setAddModalOpen(false);
    setInputValue("");
  }

  const dublicateItem = (task: any) => {
    const currentDateEvents = events.filter(event => event.start?.toDateString() === task.start.toDateString())
    const dublicatedItem = {
      id: generateGuid(),
      title: task.title,
      start: task.start,
      done: false,
      order: currentDateEvents.length
    }
    dispatch(addEvent(dublicatedItem))
    
  }

  const onCancel = () => {
    setAddModalOpen(false)
    setEditModalOpen(false)
    setInputValue("");
    currentDateEl && (currentDateEl as HTMLElement).classList.remove("selected")
  }
  const eventDidMount = function(info:any) {
    if (info.event.extendedProps.done) {
      info.el.style.textDecoration = 'line-through';
      info.el.style.opacity =  0.4;
      }
    }

  const eventAllow = function(dropInfo: any, draggedEvent: any) {
      if (draggedEvent.extendedProps.done) {
        return false;
      } else {
        return true;
      }
  }

  const events2 = [
    {
      id: "1",
      title: "qwerty",
      start: moment().startOf("day").toDate()
    },
    {
      id: "2",
      title: "qwerty",
      start: moment().startOf("day").toDate()
    }
  ]

  return (
    <div className="calendar-wrapper">
      <FullCalendar
        locale="ru"
        plugins={[dayGridPlugin, interactionPlugin]}
        initialView={props.initialView}
        weekends={true}
        events={events}
        eventContent={renderEventContent}
        editable={true}
        droppable={true}
        firstDay={1}
        dateClick={addEventHandler}
        eventClick={editEventHandler}
        eventDidMount={eventDidMount}
        eventOrder={"order"}
        contentHeight={"70vh"}
      />
      <AddTaskModal open={addModalOpen} setOpen={setAddModalOpen} inputValue={inputValue} setInputValue={setInputValue} addItem={addItem} onCancel={onCancel} />
      <AddTaskModal open={editModalOpen} setOpen={setEditModalOpen} inputValue={inputValue} setInputValue={setInputValue} addItem={editItem} onCancel={onCancel} />
    </div>
  )}


