import { useSelector } from "react-redux";
import { RootState } from "../../reducers";
import "./AllTasks.css";

export const AllTasks = () => {
    const events = useSelector((state: RootState) => state.todos.events);
    const dates =  Array.from(new Set(events.map(event => event.start?.toLocaleDateString())));
    return (
        <div className={"all-tasks__wrapper"}>
            {dates.map(date =>
                <div>
                    <div className={"all-tasks__date"}>{date}</div>
                    {events.filter(event => event.start?.toLocaleDateString() === date).map(event => <div className={"all-tasks__item"}>{event.title}</div>)}
                </div>    
            )}
            
        </div>
    )
}