import classNames from "classnames";
import { useState } from "react";
import { addEvent, IEventItem, Todo } from "../../reducers/newSlice";
import { Modal } from "../Modal/Modal";
import "./Menu.css";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { Button } from "../Button/Button";
import { useDispatch } from "react-redux";
import moment from "moment";
import dayjs from "dayjs";

export interface IMenuProps {
    editItem: () => void;
    deleteItem: (task: IEventItem) => void;
    dublicateItem: (task: IEventItem) => void;
    task: IEventItem;
}

export const Menu = (props: IMenuProps) => {
    const {editItem, deleteItem, task, dublicateItem} = props;
    const [open, setOpen] = useState<boolean>(false)
    const [dateOpen, setDateOpen] = useState<boolean>(false)
    const [coordinates, setCoordinates] = useState<{x: number,y: number}>();
    const [dateValue, setDateValue] = useState<any>();
    const dispatch = useDispatch()
    const onClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        e.preventDefault();
        setCoordinates({x: e.clientX - 180, y: e.clientY})
        setOpen(true);
    }

    const onItemClick = (e: React.MouseEvent, handler: Function) => {
        e.stopPropagation();
        handler();
        setOpen(false);
    }

    const setDate = () => {
        deleteItem(task);
        dispatch(addEvent({...task, start: dateValue.toDate()}));
        setDateOpen(false);
    }

    const buttons = [
        <Button text={""} onClick={setDate}></Button>,
        <Button text={""} onClick={()=> setDateOpen(false)}></Button>,
      ]
    return (
        <>
            <div className="menu__button" onClick={onClick}></div> 
            <Modal key="menu" open={open} setOpen={setOpen} onCancel={() => setOpen(false)} coordinates={coordinates} className={"menu__modal"}>
                <div className="menu__items">
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e, editItem)}>
                        <div className="item-list_edit-button"></div>
                        <div></div>
                    </div>
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e,()=>dublicateItem(task))}>
                        <div className="item-list_dublicate-button"></div>
                        <div></div>
                    </div>
                 
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e,()=>setDateOpen(true))}>
                            <div className="item-list_set-date-button"></div>
                            <div>{task.start ? " " : " "}</div>
                    </div>
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e,()=>deleteItem(task))}>
                        <div className="item-list_delete-button"></div>
                        <div></div>
                    </div>
                </div>
            </Modal> 
            <Modal key="date" open={dateOpen} setOpen={setDateOpen} onCancel={() => setDateOpen(false)} buttons={buttons} className={"menu__date-modal"}>
                <DatePicker onChange={(newValue)=> setDateValue(newValue)}/>
            </Modal> 
        </>
        
    )
}